// ============================================
// Shared Variables
// ============================================

$color-overlay-bg: rgba(0, 0, 0, 0.7);
$color-modal-bg-start: #1a1a2e;
$color-modal-bg-end: #16213e;
$color-primary-start: #667eea;
$color-primary-end: #764ba2;
$color-text-primary: #fff;
$color-text-secondary: #aaa;
$color-border-light: rgba(255, 255, 255, 0.1);
$color-border-medium: rgba(255, 255, 255, 0.2);
$color-input-bg: rgba(255, 255, 255, 0.05);
$color-input-bg-hover: rgba(255, 255, 255, 0.1);

$spacing-sm: 10px;
$spacing-md: 15px;
$spacing-lg: 20px;
$spacing-xl: 30px;
$spacing-xxl: 40px;

$border-radius-sm: 8px;
$border-radius-md: 10px;
$border-radius-lg: 15px;
$border-radius-xl: 20px;

$transition-default: all 0.3s ease;

$z-index-modal: 10000;

// ============================================
// Shared Mixins
// ============================================

@mixin modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $color-overlay-bg;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: $z-index-modal;
}

@mixin modal-container {
  max-width: 800px;
  width: 90%;
  max-height: 90vh;
  overflow: auto;

  .modal-wrapper {
    background: linear-gradient(135deg, $color-modal-bg-start 0%, $color-modal-bg-end 100%);
    border-radius: $border-radius-lg;
    padding: $spacing-xl;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  }
}

@mixin modal-content {
  min-height: 300px;

  h3 {
    color: $color-text-primary;
    margin-bottom: $spacing-lg;
    text-align: center;
    font-size: 24px;
  }
}

@mixin button-action {
  background: $color-input-bg;
  border: 2px solid $color-border-medium;
  color: $color-text-primary;
  padding: 10px 20px;
  border-radius: $border-radius-xl;
  font-size: 14px;
  cursor: pointer;
  transition: $transition-default;
  display: flex;
  align-items: center;
  gap: 8px;

  i {
    font-size: 14px;
  }

  &:hover {
    background: $color-input-bg-hover;
    border-color: rgba(255, 255, 255, 0.4);
    transform: translateY(-2px);
  }

  &:active {
    transform: translateY(0);
  }
}

@mixin button-primary {
  background: linear-gradient(135deg, $color-primary-start 0%, $color-primary-end 100%);
  color: $color-text-primary;
  border: none;
  padding: 12px 30px;
  border-radius: 25px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: $transition-default;

  &:hover {
    transform: scale(1.05);
    box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
  }

  &:active {
    transform: scale(0.98);
  }
}

@mixin input-control {
  width: 100%;
  background: $color-input-bg;
  border: 2px solid $color-border-medium;
  color: $color-text-primary;
  padding: 10px 12px;
  border-radius: $border-radius-sm;
  font-size: 14px;
  cursor: pointer;
  transition: $transition-default;

  &:hover {
    background: $color-input-bg-hover;
    border-color: rgba(255, 255, 255, 0.4);
  }

  option {
    background: $color-modal-bg-start;
    color: $color-text-primary;
  }
}

// ============================================
// Shared Component Classes
// ============================================

.card-actions {
  display: flex;
  gap: $spacing-md;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: $spacing-lg;

  .btn-action {
    @include button-action;
  }
}
