@import url('https://fonts.googleapis.com/css2?family=Lavishly+Yours&family=Lobster&family=Pacifico&family=Borel&display=swap');
@import './LyricCardShared.scss';

.card-config {
  text-align: left;
  flex: 0 0 280px;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 12px;
  overflow-y: auto;

  .config-group {
    margin-bottom: 12px;

    span {
      padding: 5px 0;
      display: inline-block;
    }

    &:last-child {
      margin-bottom: 0;
    }

    .button-group {
      display: flex;
      gap: 10px;

      .btn-config {
        flex: 1;
        background: $color-input-bg;
        border: 2px solid $color-border-medium;
        color: $color-text-primary;
        padding: 6px;
        border-radius: $border-radius-sm;
        cursor: pointer;
        transition: $transition-default;
        font-size: 16px;

        &:hover {
          background: rgba(255, 255, 255, 0.15);
          border-color: rgba(255, 255, 255, 0.3);
        }

        &.active {
          background: linear-gradient(135deg, $color-primary-start 0%, $color-primary-end 100%);
        }
      }
    }

    .color-inputs {
      display: flex;
      gap: 10px;
      align-items: center;

      .color-picker {
        width: 60px;
        height: 40px;
        border: 2px solid $color-border-medium;
        cursor: pointer;
        background: transparent;

        &:hover {
          border-color: rgba(255, 255, 255, 0.4);
        }
      }

      .preset-colors {
        @include input-control;
        flex: 1;
        padding: 8px 12px;
      }
    }

    .font-select {
      @include input-control;
    }

    .font-size-slider {
      width: 100%;
      height: 6px;
      border-radius: 3px;
      background: rgba(255, 255, 255, 0.2);
      outline: none;
      -webkit-appearance: none;
      appearance: none;

      &::-webkit-slider-thumb {
        -webkit-appearance: none;
        &.bg-source {
          .btn-config {
            text-transform: uppercase;
            font-weight: 600;
          }
        }
        appearance: none;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background: linear-gradient(135deg, $color-primary-start 0%, $color-primary-end 100%);
        cursor: pointer;
        border: 2px solid rgba(255, 255, 255, 0.3);
        transition: $transition-default;

        &:hover {
          box-shadow: 0 0 10px rgba(102, 126, 234, 0.5);
        }
      }

      &::-moz-range-thumb {
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background: linear-gradient(135deg, $color-primary-start 0%, $color-primary-end 100%);
        cursor: pointer;
        border: 2px solid rgba(255, 255, 255, 0.3);
        transition: $transition-default;

        &:hover {
          box-shadow: 0 0 10px rgba(102, 126, 234, 0.5);
        }
      }
    }
  }

  .config-text-color {
    display: flex;
    align-items: center;
    gap: 10px;

    .color-picker {
      border: 2px solid $color-border-medium;
      cursor: pointer;
      background: transparent;

      &:hover {
        border-color: rgba(255, 255, 255, 0.4);
      }
    }

    .bold-toggle {
      margin-left: 10px;
    }

    .bold-checkbox {
      width: 18px;
      height: 18px;
      cursor: pointer;
      accent-color: $color-primary-start;
    }
  }
}
