@import './LyricCardShared.scss';

.lyric-download-modal-overlay {
  @include modal-overlay;
}

.lyric-download-modal-container {
  @include modal-container;
}

.lyric-download-content {
  @include modal-content;

  .config-preview-row {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
    align-items: flex-start;
  }

  /* Do not set margin, otherwise html-to-image will capture unwanted space */
  .lyric-card {
    padding: 40px;
    aspect-ratio: 1 / 1;
    width: 100%;
    max-width: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;

    .card-lyric-text {
      white-space: pre-wrap;
    }

    .card-song-info {
      color: rgba(255, 255, 255, 0.8);
      font-size: 13px;
      border-top: 1px solid rgba(255, 255, 255, 0.3);
      padding-top: 10px;
      width: 100%;

      .card-title {
        font-weight: 600;
        line-height: 2;
      }

      .card-artist {
        font-weight: 400;
        font-size: 0.9em;
      }
    }
  }
}
