@import './LyricCardShared.scss';

.lyric-selection-modal-overlay {
  @include modal-overlay;
}

.lyric-selection-modal-container {
  @include modal-container;
}

.lyric-selection-content {
  @include modal-content;
  min-height: 400px;

  .instruction {
    color: #aaa;
    text-align: center;
    margin-bottom: 20px;
    font-size: 14px;
  }

  .lyric-display {
    background: rgba(255, 255, 255, 0.05);
    border: 2px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    padding: 20px;
    max-height: 400px;
    overflow-y: auto;
    color: #fff;
    font-size: 16px;
    line-height: 1.8;
    margin-bottom: 20px;
    user-select: text;
    cursor: text;

    &::selection {
      background: rgba(255, 200, 0, 0.3);
    }
  }

  .btn-generate-mobile {
    @include button-primary;
  }

  .selected-preview {
    background: rgba(100, 200, 255, 0.1);
    border: 2px solid rgba(100, 200, 255, 0.3);
    border-radius: 10px;
    padding: 20px;
    margin-top: 20px;

    h4 {
      color: #64c8ff;
      margin-bottom: 15px;
      font-size: 18px;
    }

    .preview-text {
      color: #fff;
      background: rgba(0, 0, 0, 0.3);
      padding: 15px;
      border-radius: 8px;
      margin-bottom: 15px;
      white-space: pre-wrap;
      line-height: 1.6;
    }

    .btn-generate {
      @include button-primary;
      width: 100%;
    }
  }
}
